





















export default {
    data() {
        return {
            title: 'list-with-collapses',
            lists: [{
                    title: "产品",
                    show: false,
                    item: ["iOS", "Android", "HTML5"]
                },
                {
                    title: "方案",
                    show: false,
                    item: ["PC方案", "手机方案", "TV方案"]
                },
                {
                    title: "新闻",
                    show: false,
                    item: ["公司新闻", "行业新闻"]
                }
            ]
        }
    },
    methods: {
        trigerCollapse(e) {
            for (let i = 0, len = this.lists.length; i < len; ++i) {
                if (e === i) {
                    this.lists[i].show = !this.lists[i].show;
                } else {
                    this.lists[i].show = false;
                }
            }

        }
    }
}



